<style scoped>
.titlestyle{
  text-indent: 10px;
  border-left: 4px solid #2d8cf0;
  font-size: 15px;
  line-height: 32px;
  margin-right: 20px;
  float: left;  
}
.tablebox{
    padding-top: 20px;
}
.inputwidth{
    width: 80%;
}
.title_lable{
  display: inline-block;
  min-width: 14%;
  text-align: right
}
.inputbox{
  margin-bottom: 15px;
}
.bitian{
    color: red;
    font-size: 20px;
    vertical-align: -webkit-baseline-middle;
    padding-right: 3px;
}
.pagebox{
  text-align: right;
  padding-top: 15px;
}
</style>
<template>
  <div>
    <div>
        <h1 class="titlestyle">当前库房编号：HK01</h1>
        <Button type="primary" @click="openmodel(1)">添加库位</Button>
    </div>
    <div class="tablebox">
        <Table :columns="columns1" :data="PalletArr">
          <template slot-scope="{ row, index }" slot="Indexs">
             <span>{{index+1}}</span>
          </template>
          <template slot-scope="{ row, index }" slot="Code">
             <span>{{row.PlaceIDs}}</span>
          </template>
          <template slot-scope="{ row, index }" slot="actions">
            <Button type="primary" size='small' @click="confirmDel(row.PlaceIDs)">删除库位</Button>
          </template>
        </Table>
         <div class="pagebox">
         <Page :total="total" :page-size='pageSize' :current='pageIndex' @on-change='changepage'/>
      </div>
    </div>
    <Modal
        v-model="showmodel"
        :title="titlemodel"
        @on-visible-change='changeshowmodel'>
        <div class="inputbox">
            <label for="" class="title_lable"><span class="bitian">*</span>库区：</label>
            <Select v-model="fromdata.area" filterable class="inputwidth">
                <Option v-for="(i,index) in 26" :value="String.fromCharCode(65+index)" :key="index">{{ String.fromCharCode(65+index) }}</Option>
            </Select>
        </div>
        <div class="inputbox">
            <label for="" class="title_lable"><span class="bitian">*</span>货架号：</label>
            <Select v-model="fromdata.frame" filterable class="inputwidth">
                 <Option v-for="(item,index) in 100" :value="index" :key="index">{{ index }}</Option>
            </Select>
        </div>
        <div class="inputbox">
            <label for="" class="title_lable"><span class="bitian">*</span>层号：</label>
            <Select v-model="fromdata.row" filterable class="inputwidth">
                <Option v-for="(item,index) in Numberarr" :value="item" :key="index">{{ item }}</Option>
            </Select>
        </div>
        <div class="inputbox">
            <label for="" class="title_lable"><span class="bitian">*</span>库位号：</label>
            <Select v-model="fromdata.place" filterable class="inputwidth">
                 <Option v-for="(item,index) in 100" :value="index" :key="index">{{ index }}</Option>
            </Select>
        </div>
         <div slot="footer">
              <Button @click="cancel_btn">取消</Button>
              <Button type="primary" @click="ok_btn">确定</Button>
        </div>
    </Modal>
  </div>
</template>
<script>
import {CgShowPallets,CgSetPlace,CgDelete } from '../../api/CgApi'
export default {
  data() {
    return {
      columns1: [
        {
          title: "#",
          slot: "Indexs"
        },
        {
          title: "编号",
          slot: "Code"
        },
        {
          title: "操作",
          slot: "actions",
          align: "center"
        }
      ],
      data1: [
        {
          name: "John Brown",
          age: 18,
          address: "New York No. 1 Lake Park",
          date: "2016-10-03"
        },
        {
          name: "Jim Green",
          age: 24,
          address: "London No. 1 Lake Park",
          date: "2016-10-01"
        },
        {
          name: "Joe Black",
          age: 30,
          address: "Sydney No. 1 Lake Park",
          date: "2016-10-02"
        },
        {
          name: "Jon Snow",
          age: 26,
          address: "Ottawa No. 2 Lake Park",
          date: "2016-10-04"
        }
      ],
      titlemodel:"",//弹出框title
      showmodel:false,
      Numberarr:100,
      fromdata:{
        area:"",
        frame:"",
        row:"",
        place:"",
      },
      Waybill:"",
      PalletArr:[],
      loading:true,
      total:0,
      pageIndex:1,
      pageSize:10,
    };
  },
  created() {
    this.Waybill=sessionStorage.getItem("UserWareHouse")
  },
  mounted() {
    this.setnva()
    this.CgShowPallets()
  },
  methods: {
    setnva() {
      var cc = [
        {
          title: "库位管理",
          href: ""
        }
      ];
      this.$store.dispatch("setnvadata", cc);
    },
    CgShowPallets(){
      var data={
        whid:this.Waybill,
        pageIndex:this.pageIndex,
        pageSize:this.pageSize,
      }
      CgShowPallets(data).then(res=>{
        this.PalletArr=res.obj.Data
        this.total=res.obj.Total;
        this.loading=false
      })
    },
    changepage(val){
      this.pageIndex=val;
      this.loading=true;
      this.CgShowPallets()
    },
    ok_btn(){
      // area:"",
      //   frame:"",
      //   row:"",
      //   place:"",
      console.log(this.fromdata)
      if(this.fromdata.frame==''||this.fromdata.area==''||this.fromdata.row==''||this.fromdata.place==""){
          this.$Message.error('请输入必填项');
      }else{
        var frame=(Array(2).join('0') + this.fromdata.frame).slice(-2)
        var place=(Array(2).join('0') + this.fromdata.place).slice(-2)
        var data='0'+this.fromdata.area+frame+this.fromdata.row+place;
        var obj={
          whCode:this.Waybill,//库房编号，
          place:data
        }
        CgSetPlace(obj).then(res=>{
          if(res.success==true){
            this.$Message.success('添加成功');
            this.showmodel=false;
             this.CgShowPallets()
          }else if(res.code){
             this.$Message.error('已存在');
          }
        })
      }
       
    },
    cancel_btn(){
        this.showmodel=false;
    },
    changeshowmodel(val){
      if(val==false){
         this.fromdata={
          area:"",
          frame:"",
          row:"",
          place:"",
        }
      }
    },
    openmodel(val){
        console.log(val)
        if(val==1){
            this.titlemodel='添加库位'
        }else{
            this.titlemodel='修改库位'
        }
        this.showmodel=true;
    },
    confirmDel(id){
      var id=id;
        this.$Modal.confirm({
            title: '删除',
            content: '<p>是否确认删除此库位?</p>',
            onOk: () => {
              var data={
                id:id
              }
                CgDelete(data).then(res=>{
                  if(res.success==true){
                     this.$Message.success('删除成功');
                     this.CgShowPallets()
                  }else{
                     this.$Message.error('删除失败,请联系管理员');
                  }
                })
            },
            onCancel: () => {
                this.$Message.info('取消删除');
            }
        });
    },
  }
};
</script>


